<template>
    <v-container fluid>
      <h2>Subdomínios de questões</h2>
      <v-row class="col-md-12" v-for="group in dict" :key="String(group._id['study_cycle']) + String(group._id['scholarity'])">
        <v-expansion-panels>
          <v-expansion-panel v-for="domain in group.domains" :key="String(domain.id)">
            <v-expansion-panel-header>
              <h2 style="font-size: 25px">
                {{ domain.description }}
                <small style="font-weight: 400">{{ group._id.scholarity }}</small>
              </h2>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-divider></v-divider>
              <v-data-table :headers="headers" :items="domain.subdomains" :footer-props="footer_props" :items-per-page="5" :search="search">
              <template v-slot:no-data>
                <v-alert :value="true" color="warning" icon="warning">
                  Ainda não foi possível apresentar uma lista dos subdomínios...
                </v-alert>
              </template>
              <template v-slot:item.ops="{ item }">
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn @click="atual_domain = {}, atual_domain = domain, atual_subdomain = {}, atual_subdomain = item, dialog_subdomain = true" x-small class="primary" style="width: 2px; height: 28px" v-bind="attrs" v-on="on">
                      <v-icon size="16">mdi-eye</v-icon>
                    </v-btn>
                  </template>
                  <span>Ver</span>
                </v-tooltip>
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn @click="atual_domain = {}, atual_domain = domain, atual_subdomain = {}, atual_subdomain = item, edit_subdomain = true, id_domain = domain.id, domain_description = domain.description + '#--#' + group._id.study_cycle + '#--#' + group._id.scholarity" x-small class="ma-1" color="light-blue" style="width: 2px; height: 28px" dark v-bind="attrs" v-on="on">
                      <v-icon size="16">mdi-pencil</v-icon>
                    </v-btn>
                  </template>
                  <span>Editar</span>
                </v-tooltip>
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn @click="atual_domain = {}, atual_domain = domain, atual_subdomain = {}, atual_subdomain = item, delete_subdomain = true" x-small color="red" class="ma-0" style="width: 2px; height: 28px" dark v-bind="attrs" v-on="on">
                      <v-icon dark size="16">mdi-trash-can-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>Apagar</span>
                </v-tooltip>
              </template>
            </v-data-table>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
      </v-row>
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <v-btn @click="form_subdomain = true" x-small class="ma-0" style="height: 30px" color="primary" dark v-bind="attrs" v-on="on">
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
        </template>
        <span>Novo</span>
      </v-tooltip>
      <v-dialog v-model="dialog_subdomain" persistent max-width="600px">
          <InfoSubdomain :infoDominio="atual_domain" :infoSubdominio="atual_subdomain" v-on:closeModal="dialog_subdomain = false"/>
      </v-dialog>
      <v-dialog v-model="edit_subdomain" persistent max-width="600px">
          <EditSubdomain  :infoDominio="atual_domain" :infoSubdominio="atual_subdomain" :dict="dict" :domId="id_domain" :domDescription="domain_description"
                          :idUser="id_user" :username="username" :user_type="user_type" v-on:inputData="update_value" v-on:closeModal="edit_subdomain = false"
          />
      </v-dialog>
      <v-dialog v-model="delete_subdomain" persistent max-width="600px">
          <DeleteSubdomain :infoDominio="atual_domain" v-on:deleteSubdomain="del_subdomain(atual_subdomain.id), delete_subdomain = false" v-on:closeModal="delete_subdomain = false"/>
      </v-dialog>
      <v-dialog v-model="form_subdomain" persistent max-width="600px">
          <FormSubdomain :idUser="id_user" :username="username" :user_type="user_type" :dict="dict" v-on:inputData="update_value" v-on:closeModal="form_subdomain = false"/>
      </v-dialog>
    </v-container>
</template>

<script>
import InfoSubdomain from './Info_subdomain'
import EditSubdomain from './Edit_subdomain'
import DeleteSubdomain from './Delete_subdomain'
import FormSubdomain from './Form_subdomain'

  export default {
    name: 'Subdomains_list',

    components: {
      InfoSubdomain, EditSubdomain, DeleteSubdomain, FormSubdomain
    },

    data: function () {
        return {
          domains: [], dict: [], dialog_subdomain: false, delete_subdomain: false, edit_subdomain: false, form_subdomain: false, id_domain: -1,
          domain_description: "", atual_domain: {}, atual_subdomain: {}, id_user: "", username: "", user_type: "", search: '',
          headers: [
            { text: 'Descrição', value: 'description', class: "subtitle-2" },
            { text: 'Inserido por', value: 'inserted_by_name', class: "subtitle-2" },
            { text: 'inserido a', value: 'inserted_at', class: "subtitle-2" },
            { text: 'Validado por', value: 'validated_by_name', class: "subtitle-2" },
            { text: 'Validado a', value: 'validated_at', class: "subtitle-2" },
            { text: 'Edição', value: 'ops', class: 'subtitle-1'}
          ],footer_props: {
            "items-per-page-text": "Mostrar",
            "items-per-page-options": [5, 10, 20, -1],
            "items-per-page-all-text": "Todos"
          }
        }
    },
    async created(){
      this.id_user = this.$router.history.current.query.idUser
      this.username = this.$router.history.current.query.username
      this.user_type = this.$router.history.current.query.user_type

      await this.load_subdomains()
    },
    methods: {
      async update_value() {
        this.form_subdomain = false
        this.edit_subdomain = false
        await this.load_subdomains()
      },
      async load_subdomains(){
          await this.$http.get('https://leonardo.di.uminho.pt/api/v0/questions/getSubdomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.id_user) //questions/domains')
            .then(async response => {
              await this.aux_load(response)

              console.log("Resposta:", response.data)
            })
            .catch(error => { throw(error) })
      },
      async aux_load(response){
        this.dict = []
        eval(response.data).forEach(group => {
          this.dict.push(group)
        })

        var subsObj = []

        this.dict.forEach(async group => {
          var newObj = {_id: group._id, domains: []}
          group.domains.forEach(domain => {
            var domVariables = {
              config: domain.config, 
              description: domain.description,
              id: domain.id,
              inserted_at: domain.inserted_at,
              inserted_by: domain.inserted_by,
              scholarity: domain.scholarity,
              study_cycle: domain.study_cycle,
              subsubdomains: domain.subsubdomains,
              users_in_charge: domain.users_in_charge,
              validated_at: domain.validated_at,
              validated_by: domain.validated_by,
              _id: domain._id
            }

            var subdoms = []
            domain.subdomains.forEach(subdomain => {
              subdoms.push({
                id: subdomain.id,
                description: subdomain.description,
                inserted_by_name: subdomain.inserted_by.name,
                inserted_at: String(new Date(subdomain.inserted_at).toLocaleDateString()).split('/').join('-'),
                validated_by_name: (subdomain.validated_by == "") ? "---" : subdomain.validated_by.name,
                validated_at: (subdomain.validated_by == "") ? "---" : String(new Date(subdomain.validated_at).toLocaleDateString()).split('/').join('-'),
                users_in_charge: (subdomain.users_in_charge) ? subdomain.users_in_charge : []
              })
            })
            domVariables.subdomains = subdoms
            newObj.domains.push(domVariables)
          })
          subsObj.push(newObj)
        })
        this.dict = subsObj
      },
      async del_subdomain(id){
        const headers = {
          'Content-Type': 'text/plain',
        }

        await this.$http.post('https://leonardo.di.uminho.pt/api/v0/questions/getSubdomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.id_user, id, {
          headers: headers
        })
          .then(async response => {
            await this.aux_load(response)
          })
          .catch(error => { throw(error) })
      }
    }
  }
</script>

<style>
tbody tr:nth-of-type(odd) {
  background-color: rgba(0, 0, 0, .05);
}
tbody tr:nth-of-type(odd):hover {
  background-color: rgba(0, 0, 0, .05) !important;
}
tbody tr:nth-of-type(even):hover {
  background-color: white !important;
}
</style>