import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import vuetify from './plugins/vuetify';
import VueFilterDateParse from '@vuejs-community/vue-filter-date-parse';
import VueFilterDateFormat from '@vuejs-community/vue-filter-date-format';
import VueRouter from 'vue-router'
import { store } from './store/store'
import { routes } from './routes'
import { RecordRTCPromisesHandler, StereoAudioRecorder } from 'recordrtc'
import * as VueWindow from '@hscmap/vue-window'
import VueWindowModal  from  'vue-window-modal'
import VueDraggableResizable from 'vue-draggable-resizable'

var hark = require('hark')

Vue.use(VueRouter);
Vue.use(VueFilterDateParse);
Vue.use(VueFilterDateFormat);
Vue.use(VueWindow);
Vue.use(VueWindowModal);

import 'vue-draggable-resizable/dist/VueDraggableResizable.css'

Vue.component('vue-draggable-resizable', VueDraggableResizable)

Vue.prototype.$sendToServer = async function(blob, request, speechEvents){
    const form = new FormData();
    form.append("language", "pt-PT");
    form.append("audio", blob, "file.wav");

    request.data = form;

    console.log("A enviar para processamento...")
    await this.$store.commit('set_listening', 0)
    await this.$store.commit('set_processing', 1)
    
    var action_next = -1
    var action_question = -1

    await this.$http(request)
    .then(async result => {
        var response = result.data

        if( (Object.keys(response.body).length == 0) && (String(response.params.url).includes('next')) ){
            action_next = 1
            action_question = 0
        }
        else if( (Object.keys(response.body).length > 0) && (String(response.params.url).includes('next')) ){
            action_next = 1
            action_question = response.body.answer
        }
        else if ( (!String(response.params.url).includes('next')) ){
            if( response.body.answer ){
                action_next = 0
                action_question = response.body.answer
            }
        }
    })
    .catch(async () => { await this.$store.commit('set_error_processing_voice', 1) })
    .finally(async() => {
        speechEvents.stop()

        await this.$store.commit('set_action_next', action_next)
        await this.$store.commit('set_action_question', action_question)
        await this.$store.commit('set_processing', 0)
    })
}

Vue.prototype.$capturemicrophone = async function(callback){
    navigator.mediaDevices
        .getUserMedia({audio: true})
        .then(callback)
        .catch("Erro ao capturar microfone:", console.error);
}

Vue.prototype.$listen = async function(recorder, request){
    this.$capturemicrophone(async microphone => {
        recorder = new RecordRTCPromisesHandler(microphone, {
            recorderType: StereoAudioRecorder,
            mimeType: 'audio/wav',
            disableLogs: true
        });


        recorder.startRecording();
        console.log("A ouvir...")
        await this.$store.commit('set_listening', 1)
        const speechEvents = hark(microphone, {});

        speechEvents.on('stopped_speaking', async () => {
            await recorder.stopRecording();
            console.log("Áudio recebido")

            const blob = await recorder.getBlob();

            await this.$sendToServer(blob, request, speechEvents)
        });
    });
}

const router = new VueRouter({
  routes,
  mode: 'history'
})

Vue.config.productionTip = false
Vue.prototype.$http = axios

new Vue({
  vuetify,
  router,
  store: store,
  render: h => h(App)
}).$mount('#app')
