<template>    
    <v-card>
        <v-row no-gutters>
            <v-card-title>Editar domínio</v-card-title>
            <v-spacer></v-spacer>
            <v-btn style="position: absolute; rigth: 50%; margin-top: 15px; margin-left: 550px;" icon @click="cancel()" >
                <v-icon>mdi-close</v-icon>
            </v-btn>
        </v-row>
        <v-divider></v-divider>
        <v-card-text style="position:relative; top: 25px">
            <v-textarea v-model="study_cicle" label="Ciclo de estudos" auto-grow outlined rows="1" row-height="15"></v-textarea>
            <v-spacer></v-spacer>
            <v-textarea v-model="scholarity" label="Escolaridade" auto-grow outlined rows="1" row-height="15"></v-textarea>
            <v-textarea v-model="description" label="Designação" auto-grow outlined rows="1" row-height="15"></v-textarea>
            <div v-if="domain_error == true">
                <v-alert dense outlined type="error">
                    Desinação já atribuída a um domínio
                </v-alert>
            </div>
            <h4 class="primary--text"> Responsáveis: </h4>
            <vue-tags-input v-model="tag" :tags="tags" @tags-changed="newTags => tags = newTags" style="max-width: none; width: 100%;" placeholder="Adicionar email(s)">
            </vue-tags-input>
            <div v-if="user_errors && user_errors.length > 0">
                <v-alert dense outlined type="error">
                    Verifique os seguintes emails:
                    <h4 v-for="user in user_errors" :key="user">
                        {{ user }}
                    </h4>
                </v-alert>
            </div>
            <h4 class="primary--text"> Configuração: </h4>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Nível do aluno predefinido:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="default_user_level" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="5" step="1" value=default_user_level>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"> Classificação predefinida do nível do aluno.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de desempenho incremental:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="high_performance_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="1" step="0.1" value=high_performance_factor>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Valor mínimo para subida do nível do aluno.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de desempenho decremental:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="low_performance_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="1" step="0.1" value=low_performance_factor>
                    </v-row>
                    <v-flex xs12>                
                        <p class="caption"  > Valor máximo para descida do nível do aluno.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de aptidão incremental:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="high_skill_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="1" step="0.1" value=high_skill_factor>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Valor mínimo para subida do nível do aluno.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de aptidão decremental:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="low_skill_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="1" step="0.1" value=low_skill_factor>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Valor máximo para descida do nível do aluno.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de acumulação:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="backlog_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="10" step="1" value=backlog_factor>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Intervém em respostas certas/erradas consecutivas.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de questões:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="questions_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="10" step="1" value=questions_factor>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Intervém no número de questões a responder num nível.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Mínimo de questões predefinido:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="min_questions_number" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="20" step="1" value=min_questions_number>
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Número mínimo de questões predefinido para um nível.</p>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card-text>
        <v-divider/>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn class="white--text text-capitalize; mr-2" color="#00cc99" small @click="cancel()" v-bind="attrs" v-on="on"><v-icon>mdi-cancel</v-icon></v-btn>
                </template>
                <span>Cancelar</span>
            </v-tooltip>
            <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn small class="white--text text-capitalize; mr-3" color="#00cc99" @click="submit()" v-bind="attrs" v-on="on"><v-icon>fas fa-check</v-icon></v-btn>
                </template>
                <span>Confirmar</span>
            </v-tooltip>
        </v-card-actions>
    </v-card>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';

export default {
    name: 'Edit_domain',

    props: ['infoDominio', 'idUser', 'username', 'user_type'],

    components: {
        VueTagsInput
    },

    data: function(){
        return{
            domain_error: false, user_errors: [], tag: '', tags: [], study_cicle: undefined, scholarity: undefined, description: undefined, users_in_charge: undefined,
            backlog_factor: 0, default_user_level: 0, high_performance_factor: 0, high_skill_factor: 0, low_performance_factor: 0, low_skill_factor: 0, min_questions_number: 0, questions_factor: 0
        };
    },
    created(){
        this.domain_error = false
        this.user_errors = []
        this.study_cicle = this.infoDominio.study_cycle
        this.scholarity = this.infoDominio.scholarity
        this.description = this.infoDominio.description
        this.tags = []
        this.infoDominio.users_in_charge.forEach(element => {
            this.tags.push({
                text: String(element.email),
                tiClasses: ["ti-valid"]
            })
        })
        this.backlog_factor= this.infoDominio.config.backlog_factor, this.default_user_level = this.infoDominio.config.default_user_level
        this.high_performance_factor = this.infoDominio.config.high_performance_factor, this.high_skill_factor = this.infoDominio.config.high_skill_factor
        this.low_performance_factor = this.infoDominio.config.low_performance_factor, this.low_skill_factor = this.infoDominio.config.low_skill_factor
        this.min_questions_number = this.infoDominio.config.min_questions_number, this.questions_factor = this.infoDominio.config.questions_factor
    },
    watch: {
        infoDominio: function(){
            console.log("Info subdomínio mudou para:", this.infoDominio)
            if(this.infoDominio != {}){
                this.study_cicle = this.infoDominio.study_cycle
                this.scholarity = this.infoDominio.scholarity
                this.description = this.infoDominio.description
                this.tags = []
                this.infoDominio.users_in_charge.forEach(element => {
                    this.tags.push({
                        text: String(element.email),
                        tiClasses: ["ti-valid"]
                    })
                })
                this.backlog_factor= this.infoDominio.config.backlog_factor, this.default_user_level = this.infoDominio.config.default_user_level
                this.high_performance_factor = this.infoDominio.config.high_performance_factor, this.high_skill_factor = this.infoDominio.config.high_skill_factor
                this.low_performance_factor = this.infoDominio.config.low_performance_factor, this.low_skill_factor = this.infoDominio.config.low_skill_factor
                this.min_questions_number = this.infoDominio.config.min_questions_number, this.questions_factor = this.infoDominio.config.questions_factor
            }
        }
    },
    methods: {
        submit: async function() {
            this.domain_error = false
            this.user_errors = []
            var users_in_charge = []
            if(this.tags.length > 0){
                this.tags.forEach(element => {
                    users_in_charge.push(element['text'])
                })
            }
            
            var newDomain = {
                id: this.infoDominio.id,
                study_cycle: this.study_cicle ? this.study_cicle : "",
                scholarity: this.scholarity ? this.scholarity : "",
                description: this.description ? this.description : "",
                users: users_in_charge,
                default_user_level: this.default_user_level,
                high_performance_factor: this.high_performance_factor,
                low_performance_factor: this.low_performance_factor,
                high_skill_factor: this.high_skill_factor,
                low_skill_factor: this.low_skill_factor,
                backlog_factor: this.backlog_factor,
                questions_factor: this.questions_factor,
                min_questions_number: this.min_questions_number
            }

            await this.$http.post('https://leonardo.di.uminho.pt/api/v0/questions/getDomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.idUser + '&nomeUser=' + this.username + '&editar=true', newDomain)
                .then(async response => {
                    var resposta = JSON.parse(response.data)
                
                    this.domain_error = resposta.domain_error
                    this.user_errors = resposta.user_errors

                    if((resposta.domain_error == false) && (resposta.user_errors.length == 0)){
                        this.$emit("inputData", response.dictionary);
                    }
                })
                .catch(error => { throw(error) })
        },
        cancel: function() {
            this.$emit("closeModal")
        }
    }
}
</script>

