.radial-gradient-1 {
  width: 100%;
  height: 100%;
  background-image: radial-gradient(
    ellipse farthest-corner at 45px 45px,
    #00ffff 0%,
    rgba(0, 0, 255, 0) 50%,
    #0000ff 95%
  );
}
.radial-gradient-2 {
  width: 100%;
  height: 100%;
  background-image: radial-gradient(
    farthest-corner at 45px 45px,
    #ff0000 0%,
    #0000ff 100%
  );
}
table {
  border-collapse: collapse;
}
td {
  text-align: center;
  color: rgba(0, 0, 0, 0.25);
}
th {
  color: white;
  background-color: #000;
}
.blockPointerEvents {
  pointer-events: none;
}
