<template>
    <v-container fluid>
      <v-card-title>Domínios de questões
        <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Procurar" single-line hide-details></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="domains" :footer-props="footer_props" :items-per-page="5" :search="search">
      <template v-slot:no-data>
        <v-alert :value="true" color="warning" icon="warning">
          Ainda não foi possível apresentar uma lista dos domínios...
        </v-alert>
      </template>

      <template v-slot:item.ops="{ item }">
        <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn @click="atual_domain = {}, atual_domain = item, dialog_domain = true" x-small class="primary" style="width: 2px; height: 28px" v-bind="attrs" v-on="on">
                <v-icon size="17">mdi-eye</v-icon>
              </v-btn>
            </template>
            <span>Ver</span>
        </v-tooltip>
        <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn @click="atual_domain = {}, atual_domain = item, edit_domain = true" x-small class="ma-1" style="width: 2px; height: 28px" color="light-blue" dark v-bind="attrs" v-on="on">
                <v-icon size="17">mdi-pencil</v-icon>
              </v-btn>  
            </template>
            <span>Editar</span>
        </v-tooltip>
        <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn @click="delete_domain = true, atual_domain = {}, atual_domain = item" x-small class="ma-0" style="width: 2px; height: 28px" color="red" dark v-bind="attrs" v-on="on">
                <v-icon dark size="17">mdi-trash-can-outline</v-icon>
              </v-btn>
            </template>
            <span>Apagar</span>
        </v-tooltip>
      </template>
      </v-data-table>
      <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
          <v-btn @click="form_domain = true" color="primary" x-small class="ma-1" style="height: 30px" dark v-bind="attrs" v-on="on">
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
          </template>
          <span>Novo</span>
      </v-tooltip>
      <v-dialog v-model="dialog_domain" persistent max-width="600px">
          <InfoDomain :infoDominio="atual_domain" v-on:closeModal="dialog_domain = false"/>
      </v-dialog>
      <v-dialog v-model="edit_domain" persistent max-width="600px">
          <EditDomain :infoDominio="atual_domain" :idUser="id_user" :username="username" :user_type="user_type" v-on:inputData="update_value" v-on:closeModal="edit_domain = false"/>
      </v-dialog>
      <v-dialog v-model="delete_domain" persistent max-width="600px">
          <DeleteDomain :infoDominio="atual_domain" v-on:deleteDomain="del_domain(atual_domain.id), delete_domain = false" v-on:closeModal="delete_domain = false"/>
      </v-dialog>
      <v-dialog v-model="form_domain" persistent max-width="600px">
          <FormDomain :load="true" :idUser="id_user" :username="username" :user_type="user_type" v-on:inputData="update_value" v-on:closeModal="form_domain = false"/>
      </v-dialog>
    </v-container>
</template>

<script>
import InfoDomain from './Info_domain'
import EditDomain from './Edit_domain'
import DeleteDomain from './Delete_domain'
import FormDomain from './Form_domain'

  export default {
    name: 'Dominios',

    components: {
      InfoDomain, EditDomain, DeleteDomain, FormDomain
    },

    data: function () {
        return {
          search: '',
          headers: [
            { text: 'Ciclo de estudos', value: 'study_cycle', class: "subtitle-2" },
            { text: 'Escolaridade', value: 'scholarity', class: "subtitle-2" },
            { text: 'Descrição', value: 'description', class: "subtitle-2" },
            { text: 'Inserido por', value: 'inserted_by_name', class: "subtitle-2" },
            { text: 'Data de inserção', value: 'inserted_at', class: "subtitle-2" },
            { text: 'Validado por', value: 'validated_by', class: "subtitle-2" },
            { text: 'Data de validação', value: 'validated_at', class: "subtitle-2" },
            { text: 'Edição', value: 'ops', class: 'subtitle-1'}
          ],footer_props: {
            "items-per-page-text": "Mostrar",
            "items-per-page-options": [5, 10, 20, -1],
            "items-per-page-all-text": "Todos"
          },
          id_user: -1, username: "", user_type: -1, domains: [], dialog_domain: false, edit_domain: false, delete_domain: false, form_domain: false, atual_domain: {}
        }
    },
    async created(){
      this.id_user = this.$router.history.current.query.idUser
      this.username = this.$router.history.current.query.username
      this.user_type = this.$router.history.current.query.user_type

      this.load_domains()
    },
    methods: {
      async update_value() {
        this.form_domain = false
        this.edit_domain = false
        this.load_domains()
      },
      aux_load(response, type){
        var obj = (type == "load") ? (eval(response.data)) : (JSON.parse(response.data)).dictionary
        
        obj.forEach(element => {
          var dominio = element.domains[0]
          var validado_por = "--"
          var data_validacao = "--"

          if(dominio.validated_by != ''){
            validado_por = dominio.validated_by.name
            data_validacao = String(new Date(dominio.validated_at).toLocaleDateString()).split('/').join('-')
          } 
          
          var subdominios = []
          eval(dominio.subdomains).forEach(element => {
            subdominios.push(element.description)
          })

          var responsaveis = []

          eval(dominio.users_in_charge).forEach(element => {
            responsaveis.push({
              id: element.id, 
              name: element.name,
              email: element.email,
              since: (element.since != "") ? String(new Date(element.since).toLocaleDateString()).split('/').join('-') : "",
              until: (element.until != "") ? String(new Date(element.until).toLocaleDateString()).split('/').join('-') : ""
            })
          })

          this.domains.push({
            id: dominio.id,
            study_cycle: dominio.study_cycle,
            scholarity: dominio.scholarity,
            description: dominio.description,
            inserted_by_name: (dominio.inserted_by) ? (dominio.inserted_by.name) : "",
            inserted_at: String(new Date(dominio.inserted_at).toLocaleDateString()).split('/').join('-'),
            validated_by: validado_por,
            validated_at: data_validacao,
            subdomains: subdominios,
            config: dominio.config,
            users_in_charge: responsaveis
          })
        })        
      },
      async load_domains(){
        this.domains = []
        await this.$http.get('https://leonardo.di.uminho.pt/api/v0/questions/getDomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.id_user) //questions/domains')
          .then(response => {
            this.aux_load(response, "load")
          })
          .catch(error => { throw(error) })
      },
      async del_domain(id){
        this.domains = []
        const headers = {
          'Content-Type': 'text/plain',
        }
        await this.$http.post('https://leonardo.di.uminho.pt/api/v0/questions/getDomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.id_user, id, {
          headers: headers
        })
          .then(response => {
            this.aux_load(response, "delete")
          })
          .catch(error => { throw(error) })
      }
    }
  }
</script>

<style>
tbody tr:nth-of-type(odd) {
  background-color: rgba(0, 0, 0, .05);
}
tbody tr:nth-of-type(odd):hover {
  background-color: rgba(0, 0, 0, .05) !important;
}
tbody tr:nth-of-type(even):hover {
  background-color: white !important;
}
</style>