<template>    
    <v-card>
        <v-row no-gutters>
            <v-card-title>Editar subdomínio</v-card-title>
            <v-spacer></v-spacer>
            <v-btn style="position: absolute; rigth: 50%; margin-top: 15px; margin-left: 550px;" icon @click="cancel()" >
                <v-icon>mdi-close</v-icon>
            </v-btn>
        </v-row>
        <v-divider></v-divider>
            <v-card-text style="margin-top: 15px;">
                <p>Domínio atual: {{ current_description }}</p>
                <select v-model="value" class="outlined">
                    <option value="">-- Altere o domínio --</option>
                    <optgroup :label= group._id.scholarity v-for="group in dict" :key="group._id.study_cycle + '#--#' + group._id.scholarity">
                        <option :value="domain.description + '#--#' + group._id.study_cycle + '#--#' + group._id.scholarity" v-for="domain in group.domains" :key="String(domain.id)">
                            {{ domain.description }}
                        </option>
                    </optgroup>
                </select>
            <p class="text-warning" style="margin-bottom: 0px"><small>Altere, se preferir, o domínio associado a este subdomínio.</small></p>
            </v-card-text>
            <v-card-text>
                <v-textarea v-model="description" label="Novo subdomínio" auto-grow dense outlined rows="1" row-height="15"></v-textarea>
                <div v-if="subdomain_error == true">
                    <v-alert dense outlined type="error">
                        *Desginação já atribuída a um subdomínio
                    </v-alert>
                </div>
                <h4 class="primary--text"> Operadores: </h4>
                <vue-tags-input v-model="tag" :tags="tags" @tags-changed="newTags => tags = newTags" style="max-width: none; width: 100%;" placeholder="Adicionar email(s)"/>
                <div v-if="user_errors && user_errors.length > 0">
                    <v-alert dense outlined type="error">
                        <p v-for="user in user_errors" :key="user">
                            O endereço de email *{{ user }} não corresponde a nenhum utilizador conhecido
                        </p>
                    </v-alert>
                </div>
            </v-card-text>
            <v-divider/>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn class="white--text text-capitalize; mr-2" color="#00cc99" small @click="cancel()" v-bind="attrs" v-on="on"><v-icon>mdi-cancel</v-icon></v-btn>
                    </template>
                    <span>Cancelar</span>
                </v-tooltip>
                <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn small class="white--text text-capitalize; mr-3" color="#00cc99" @click="submit()" v-bind="attrs" v-on="on"><v-icon>fas fa-check</v-icon></v-btn>
                    </template>
                    <span>Confirmar</span>
                </v-tooltip>
            </v-card-actions>
    </v-card>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';

export default {
    name: 'Edit_subdomain',

    props: ['infoDominio', 'infoSubdominio', 'domId', 'domDescription', 'idUser', 'username', 'user_type', 'dict'],

    components: {
        VueTagsInput
    },

    data: function(){
        return{
            subdomain_error: false, user_errors: [], tag: '', tags: [], scholarity: undefined, description: undefined,
            users_in_charge: undefined, value: "", current_description: "", current_study_cycle: "", current_scholarity: ""
        };
    },
    created(){
        this.domain_error = false
        this.user_errors = []
        this.tags = []
        this.infoSubdominio.users_in_charge.forEach(element => {
            this.tags.push({
                text: String(element.email),
                tiClasses: ["ti-valid"]
            })
        })
        this.current_description = String(this.domDescription).split('#--#')[0]
        this.current_study_cycle = String(this.domDescription).split('#--#')[1]
        this.current_scholarity = String(this.domDescription).split('#--#')[2]

        this.description = this.infoSubdominio.description
    },
    watch: {
        infoSubdominio: function(){
            this.domain_error = false
            this.user_errors = []
            this.tags = []
            this.infoSubdominio.users_in_charge.forEach(element => {
                this.tags.push({
                    text: String(element.email),
                    tiClasses: ["ti-valid"]
                })
            })
            this.description = this.infoSubdominio.description
        }
    },
    methods: {
        submit: async function() {
            var users_in_charge = []
            if(this.tags.length > 0){
                this.tags.forEach(element => {
                    users_in_charge.push(element['text'])
                })
            }

            var jsonObj = {
                currentDomain: {
                    idCurrent_Domain: this.domId,
                    description: this.current_description,
                    study_cycle: this.current_study_cycle,
                    scholarity: this.current_scholarity
                },
                domain: {
                    study_cycle: String(this.value).split('#--#')[1] == undefined ? "" : String(this.value).split('#--#')[1],
                    scholarity: String(this.value).split('#--#')[2] == undefined ? "" : String(this.value).split('#--#')[2],
                    description: String(this.value).split('#--#')[0] == undefined ? "" : String(this.value).split('#--#')[0]
                },
                study_cycle: String(this.value).split('#--#')[1] == undefined ? "" : String(this.value).split('#--#')[1],
                scholarity: String(this.value).split('#--#')[2] == undefined ? "" : String(this.value).split('#--#')[2],
                subdomain: this.description,
                idSubdomain: this.infoSubdominio.id,
                users: users_in_charge
            }            

            await this.$http.post('https://leonardo.di.uminho.pt/api/v0/questions/getSubdomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.idUser + '&nomeUser=' + this.username + '&editar=true', jsonObj)
                .then(async response => {
                    var resposta = JSON.parse(response.data)

                    console.log("Dicionário retornado:", resposta.dictionary)

                    this.subdomain_error = resposta.subdomain_error

                    this.user_errors = resposta.user_errors

                    if((resposta.subdomain_error == false) && (resposta.user_errors.length == 0)){
                        this.$emit("inputData");
                        this.load = false
                        this.subdomain_error =  false,
                        this.user_errors = [],
                        this.tag = '',
                        this.tags = [],
                        this.scholarity = undefined,
                        this.description = undefined,
                        this.users_in_charge = undefined
                    }
                })
                .catch(error => { throw(error) })
        },
        cancel: function() {
            this.$emit("closeModal")
        }
    }
}
</script>

