                        <input v-model="backlog_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="10" step="1" value="0">
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Intervém em respostas certas/erradas consecutivas.</p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Fator de questões:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="questions_factor" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="10" step="1" value="0">
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Intervém no número de questões a responder num nível.</p>
                    </v-flex>
