            </v-container>
            <v-container>
                <v-layout row wrap>
                    <h5 class="primary--text"> Mínimo de questões predefinido:</h5>
                    <v-row style="margin-left: 5px">
                        <input v-model="min_questions_number" style="border: 1px solid; border-radius: 3px; margin-left: 5px; text-indent: 3px" type="number" min="0" max="20" step="1" value="0">
                    </v-row>
                    <v-flex xs12>
                        <p class="caption"  > Número mínimo de questões predefinido para um nível.</p>
