                <v-btn class="white--text text-capitalize; mr-2" color="#00cc99" small @click="cancel()" v-bind="attrs" v-on="on"><v-icon>mdi-cancel</v-icon></v-btn>
            </template>
            <span>Cancelar</span>
        </v-tooltip>
        <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
                <v-btn class="white--text text-capitalize; mr-3" color="#00cc99" small @click="submit()" v-bind="attrs" v-on="on"><v-icon>fas fa-check</v-icon></v-btn>
            </template>
            <span>Confirmar</span>
        </v-tooltip>
    </v-card-actions>
    </v-card>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';

export default {
    name: 'Form_domain',

    props: ['load', 'idUser', 'username', 'user_type'],

    components: {
        VueTagsInput
    },

    data: function(){
        return{
            domain_error: false, user_errors: [], tag: '', tags: [], study_cicle: undefined, scholarity: undefined, description: undefined, users_in_charge: undefined, default_user_level: 0,
            high_performance_factor: 0, low_performance_factor: 0, high_skill_factor: 0, low_skill_factor: 0, backlog_factor: 0, questions_factor: 0, min_questions_number: 0
        };
    },
    created(){
        this.formDomain = ""
        this.domain_error = false
        this.user_errors = []
        this.tags = []
    },
    methods: {
        submit: async function() {
            this.domain_error = false
            this.user_errors = []
            var users_in_charge = []
            if(this.tags.length > 0){
                this.tags.forEach(element => {
                    users_in_charge.push(element['text'])
                })
            }
            
            var newDomain = {
                study_cycle: this.study_cicle ? this.study_cicle : "",
                scholarity: this.scholarity ? this.scholarity : "",
                description: this.description ? this.description : "",
                users: users_in_charge,
                default_user_level: this.default_user_level,
                high_performance_factor: this.high_performance_factor,
                low_performance_factor: this.low_performance_factor,
                high_skill_factor: this.high_skill_factor,
                low_skill_factor: this.low_skill_factor,
                backlog_factor: this.backlog_factor,
                questions_factor: this.questions_factor,
                min_questions_number: this.min_questions_number
            }
            await this.$http.post('https://leonardo.di.uminho.pt/api/v0/questions/getDomainsByCharge?user_type=' + this.user_type + '&idUser=' + this.idUser + '&nomeUser=' + this.username, newDomain)
                .then(async response => {
                    var resposta = JSON.parse(response.data)
                
                    this.domain_error = resposta.domain_error
                    this.user_errors = resposta.user_errors

                    if((resposta.domain_error == false) && (resposta.user_errors.length == 0)){
                        this.$emit("inputData");
                        this.load = false
                        this.domain_error =  false,
                        this.user_errors = [],
                        this.tag = '',
                        this.tags = [],
                        this.study_cicle = undefined,
                        this.scholarity = undefined,
                        this.description = undefined,
                        this.users_in_charge = undefined,
                        this.default_user_level = 0,
                        this.high_performance_factor = 0,
                        this.low_performance_factor = 0,
                        this.high_skill_factor = 0,
                        this.low_skill_factor = 0,
                        this.backlog_factor = 0,
                        this.questions_factor = 0,
                        this.min_questions_number = 0
                    }
                })
                .catch(error => { throw(error) })
        },
        cancel: function() {
            this.$emit("closeModal")
        }
    }
}
</script>

